const INITIAL_STATE = {
    currentUser: null,
    hasDoneOnboarding: false,
    hasDoneProfile: false,
    hasDoneTerms: false,
}

const userReducer = (currentState = INITIAL_STATE, action) =>{
    const {type, payload} = action;
    switch(type){
        case 'SET_CURRENT_USER':
            return {
                ...currentState,
                currentUser: payload
            }

        case 'SET_PROFILE_COMPLETE':
            return {
                ...currentState,
                hasDoneProfile: true
            }

        case 'SET_TERMS_ACCEPTED':
            return {
                ...currentState,
                hasDoneTerms: true
            }

        case 'SET_ONBOARDING_DONE':
            return {
                ...currentState,
                hasDoneOnboarding: true
            }

        default:
            return currentState;
    }
}

export default userReducer;